.landing_page_header {
    @include displayFlex(row, space-between, center);
    padding: 15px 10px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: calc(100vw - 40px);
    background: transparent;

    .page_header_logo {
        @include displayFlex(row, center, center);
        .page_header_title {
            font-size: 3vh;
            font-family: $website_name_font;
            text-decoration: none;
        }
        cursor: pointer;
    }

    .page_header_nav {
        width: 30%;
        .header_nav_list {
            @include displayFlex(row, space-around, center);

            .nav_list_item {
                font-size: 2vh;
                font-family: $poppins_font;
                a {
                    color: black;
                    text-decoration: none;
                    transition: all 0.3s ease-in;
                }
            }
        }
    }

    .page_header_btn {
        outline: none;
        border: none;
        background-color: #4fff40;
        color: black;
        padding: 10px 20px;
        border-radius: 5px;
        font-size: 1.5vh;
        text-decoration: none;
        cursor: pointer;
        font-family: $poppins_font;
        transition: all 0.3s ease-in-out;

        &:hover {
            box-shadow: 0px 3px 18px rgba(20, 255, 0, 0.74);
        }
    }
}
