@import "../components/cp_login-form";

.wrapper_login_page {
    width: 100vw;
    height: 100vh;

    display: grid;
    place-items: center;

    .login_page_form {
        width: 23%;
        @include displayFlex(column, center, stretch);

        .page_form_thirdParty {
            @include displayFlex(column, center, stretch);
            margin-top: 20px;

            .form_thirdParty_section {
                @include googleAuthBtn();
            }
        }

        .page_form_links {
            @include displayFlex(row, space-between, center);
            margin: 10px 0;

            a {
                font-size: 1vw;
                font-family: $barlow_font;
                color: #40a137;
                text-decoration: none;
                transition: all 0.3s ease-in;
            }
        }

        .page_form_div {
            @include displayFlex(column, center, stretch);

            &:not(:last-child) {
                margin-bottom: 20px;
            }

            label {
                font-size: $font_size_1;
                font-family: $barlow_font;
                margin-bottom: 10px;
            }

            input {
                background: #ffffff;
                border: 1px solid rgba(0, 0, 0, 0.4);
                border-radius: 6px;
                padding: 10px;
                font-family: $barlow_font;
                font-size: $font_size_1;
                outline: none;
                transition: all 0.3s ease-in;
            }
            .form_div_password_input {
                border: 1px solid rgba(0, 0, 0, 0.4);
                border-radius: 6px;
                @include displayFlex(row, flex-start, center);
                input {
                    border: none;
                    width: 100%;
                    // justify-self: auto;
                }
                button {
                    margin-left: auto;
                    margin-right: 20px;
                    border: none;
                    background: none;
                    cursor: pointer;
                    transition: all 0.3s ease-in;
                    display: grid;
                    place-items: center;
                }
            }
        }

        .page_form_btn {
            @include formButton();
        }
    }
}
