.cp_newsstand_wrapper {
    .wrapper_title {
        display: flex;
        justify-content: flex-start;
        gap: 29px;
        align-items: center;

        h1 {
            font-family: $montserrat_font;
            font-size: 2vw;
            font-weight: 700;
            line-height: 1.2;
            color: $title_color_green;
        }
    }

    .wrapper_newsCard_list {
        @include displayFlex(column, flex-start, center);
        gap: 20px;
        margin-top: 20px;

        .wrapper_newsCard_item {
            background: #ffffff;
            border: 1px solid rgba(0, 0, 0, 0.2);
            border-radius: 10px;
            padding: 10px 20px;
            cursor: pointer;
            width: 90%;

            a {
                color: #000;
            }

            .newsCard_item_head {
                @include displayFlex(row, space-between, center);
                margin-bottom: 20px;
                user-select: none;

                .item_head_date {
                    background: #ffffff;
                    border: 1px solid #209f15;
                    border-radius: 10px;
                    padding: 5px 10px;

                    p {
                        font-family: $barlow_font;
                        font-style: italic;
                        font-size: 1vw;
                        font-weight: 400;
                    }
                }

                .item_head_category {
                    font-family: $barlow_font;
                    font-style: italic;
                    font-weight: 400;
                    color: rgba(0, 0, 0, 0.67);
                    font-size: 1vw;
                }
            }

            .newsCard_item_content {
                font-family: $barlow_font;
                font-weight: 400;
                font-size: 1.2vw;
            }
        }
    }
}
